.container-skills {
  display: flex;
  height: 90vh;
  background-color: #FFD454;
  align-items: center;
  flex-direction: column;
  background-image: linear-gradient(149deg, transparent 88%, #0c397c 88.01%), linear-gradient(30deg, #0b336f 12%, transparent 12.01%);
  background-size: 150px 100%;
  justify-content: space-evenly;
}

.container-skills span {
  transition: revert;
  color: #fac117;
  font-size: 3rem;
  text-shadow: 2px 2px #c09e39;
  font-weight: 600;
  transition: all 300ms ease-in-out;
}

.fade-out { opacity: 0; }

.text-transition {
  opacity: 1;
  transition: opacity 0.3s ease-in-out;
}

.progress-bar {
  height: 40px;
  padding: 5px;
  background-color: rgb(201 164 50 / 57%);
  display: flex;
  width: 20%;
  justify-content: flex-start;
  align-items: center;
  border-radius: 5px;
}

.progress-bar-filling {
  height: 30px;
  transition: all 0.4s ease-out;
  box-shadow: 0 0 5px 3px #00000029;
  border-radius: 2px;
}

.container-title {
  display: flex;
  align-content: center;
  width: 100vw;
  justify-content: center;
  align-items: center;
  gap: 15px;
  flex-grow: 1;
}

.container-title h1 {
  color: #e36784;
  font-size: 3.5vw;
  text-align: center;
  margin: 0;
  text-shadow: 0.2vw 0.3vh 1px #b02b4a;
}

.container-title hr {
  color: #e3678485;
  border: 2px solid;
  border-radius: 5px;
  width: 10vw;
  margin: 0;
}

.carousel-wrapper {
  position: relative;
  padding: 15px;
  max-width: 60vw;
  height: 280px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  flex-grow: 0;
}

.gallery-wrapper {
  overflow-x: hidden;
}

.arrow-left,
.arrow-right {
  position: absolute;
  top: 0;
  left: 0;
  right: auto;
  bottom: 0;
  font-size: 20px;
  line-height: 250px;
  width: 50px;
  color: #fff;
  transition: all 600ms ease-in-out;
  background: linear-gradient(to left, transparent 0%, #e36784 200%);
  opacity: 0.5;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  z-index: 10;
}

.arrow-left:hover,
.arrow-right:hover {
  opacity: 1;
}

.arrow-right {
  right: 0;
  left: auto;
  text-align: right;
  background: linear-gradient(to right, transparent 0%, #e36784 200%);
}

.gallery {
  display: flex;
  flex-flow: row nowrap;
  gap: 15px;
}

.progress-container {
  width: -webkit-fill-available;
  display: flex;
  align-items: center;
  flex-direction: column;
  flex-grow: 2;
  margin-top: 50px;
}

.item {
  width: 200px;
  height: 200px;
  flex-shrink: 0;
  transition: all 600ms ease;
  opacity: 0.5;
  border-radius: 10px;
  margin: 30px 0;
  box-shadow: 5px 5px 5px #00000080;
}

.current-item {
  opacity: 1;
  margin: 0;
  width: 250px;
  height: 250px;
}

.colchetes-left,
.colchetes-right {
  position: absolute;
  left: -10vw;
  right: auto;
  width: 4vw;
}

.colchetes-right {
  left: auto;
  right: -10vw;
}

.cyan {
  background: cyan;
}

.deeppink {
  background: deeppink;
}

.turquoise {
  background: turquoise;
}

.darkblue {
  background: darkblue;
}

.purple {
  background: purple;
}
